import React from "react";
import Author from "./Author";
import Comments from "./Comments";
import Content from "./Content";
import Tags from "./Tags";
import StyledLink from "../Link";
import "./Course.scss";

const Course = ({ course }) => {
  const { tags, title } = course.frontmatter;

  const { html } = course;
  const { tagSlugs } = course.fields;

  return (
    <div className="Course">
      <div className="Course-homeButton">
        <StyledLink variation={"pill"} path={"/courses"}>
          All Courses
        </StyledLink>
      </div>

      <div className="Course-content">
        <Content body={html} title={title} />
      </div>

      <div className="Course-footer">
        <Tags tags={tags} tagSlugs={tagSlugs} />
        <Author />
      </div>

      <div className="Course-comments">
        <Comments
          courseSlug={course.fields.slug}
          courseTitle={course.frontmatter.title}
        />
      </div>
    </div>
  );
};

export default Course;
